gital, los dilemas éticos se presentan como una constante que exige un análisis profundo y comprometido. En este contexto, el estudio de temas como la transformación tecnológica, la automatización y la inclusión digital se vuelve esencial para entender las implicaciones sociales y éticas que estos fenómenos traen consigo, así como para diseñar respuestas que promuevan una transición laboral inclusiva y equitativa. A continuación, el PDF Dilemas éticos, explora estas temáticas, ofreciendo un enfoque crítico sobre la necesidad de armonizar el progreso tecnológico con la protección de los derechos laborales y la dignidad humana.")]),_c('a',{staticClass:"anexo mb-4 bg-white w-fit",attrs:{"href":_vm.obtenerLink('/downloads/anexo1.pdf'),"target":"_blank","data-aos":"flip-up"}},[_c('div',{staticClass:"anexo__icono",style:({'background-color': '#FCDFDB'})},[_c('img',{attrs:{"src":require("@/assets/template/icono-pdf.svg")}})]),_vm._m(2)])])])])]),_c('div',{staticClass:"bg-full-width border-top color-primario"},[_c('div',{staticClass:"p-4 p-md-5"},[_c('h2',{attrs:{"data-aos":"fade-left"}},[_vm._v("MATERIAL COMPLEMENTARIO")]),_c('div',{staticClass:"row material-complementario"},[_c('div',{staticClass:"col-12 col-md-6 col-lg-7"},[_c('p',[_vm._v("Los invitamos a explorar el material complementario de este curso, en esta sección encontrará recursos que le permitirán profundizar y enriquecer su aprendizaje en los temas tratados en esta unidad.")]),_c('p',{staticClass:"d-flex my-4"},[_c('img',{staticClass:"me-3",style:({'max-width':'16px'}),attrs:{"src":require("@/assets/componentes/link.svg")}}),_c('a',{attrs:{"href":"https://elibro.net/es/lc/tecnologicadeloriente/titulos/51423","target":"_blank","rel":"noopener noreferrer"}},[_vm._v("García Peregrín, E. (2018). Bioética: temas para el debate. Vol. 2: ( ed.). Bubok Publishing S.L.")])]),_c('p',{staticClass:"d-flex my-4"},[_c('img',{staticClass:"me-3",style:({'max-width':'16px'}),attrs:{"src":require("@/assets/componentes/link.svg